<template>
	<view>
		<view class="login-img">
			<view class="owl-login" :class="hideEyes?'password':''">
				<view class="hand"></view>
				<view class="hand hand-r"></view>
				<view class="arms">
					<view class="arm"></view>
					<view class="arm arm-r"></view>
				</view>
			</view>
		</view>
		<view class="login-form">
			<form>
				<view class="form-input">
					<input type="text" v-model="loginForm.user" value="" placeholder="手机号" />
				</view>
				<view class="form-input">
					<input type="password" v-model="loginForm.pwd" value="" placeholder="密码" @focus="passwordF_B" @blur="passwordF_B" />
				</view>
				<button class="bg-orange" form-type="submit" @tap="handleLogin">{{ loading ? "登录中...":"登 录"}} </button>
			</form>
		</view>
	</view>
</template>

<script>
	import reqAddress from 'common/reqAddress.js'
	var Signature = require("fucking-util-signature-all");
	//生成公私钥
	let { publicKey, privateKey } = Signature.RSA.generateKeys ( 1024, 'pkcs8' );
	// console.log ( publicKey, privateKey );
	
	let rsa = new Signature.RSA();
	
	export default {
		data() {
			return {
				public_key: '',
				loginForm: {
					user: '',
					pwd: ''
				},
				loading: false,
				hideEyes: false
			};
		},
		onLoad() {
			
		},
		methods: {
			//处理登录
			handleLogin() {
				this.loading = true;
				
				if(!this.checkMobile(this.loginForm.user)){//检测手机号码格式
					uni.showToast({
						title: '请输入正确的手机号码',
						duration: 1000,
						icon: 'none'
					})
					this.loading = false;
					return;
				}else if(this.loginForm.pwd === ''){//检测密码
					uni.showToast({
						title: '请填写密码',
						duration: 1000,
						icon: 'none'
					})
					this.loading = false;
					return;
				}else{
					// uni.request({
					// 	url: reqAddress.DomainName + '/validator/checkPwd',
					// 	method:'GET',
					// 	data:{
					// 		phone : this.loginForm.user,
					// 		oriPwd : this.loginForm.pwd,
					// 		pwd : this.loginForm.pwd
					// 	},
					// 	success: (res) => {
					// 		if(res.data.valid == true){
					// 			this.doLogin();
					// 		}else{
					// 			uni.showModal({
					// 				title:'登录失败',
					// 				content:'用户名密码错误或用户不存在或已注销',
					// 				showCancel:false,
					// 				success: (res) => {
					// 					if(res.confirm){
					// 						this.loading = false;
					// 					}
					// 				}
					// 			})
					// 		}
					// 	},
					// 	fail: () => {
					// 		this.loading = false;
					// 		uni.showToast({
					// 			title:"请检查网络",
					// 			icon:"none"
					// 		})
					// 	}
					// })
					this.doLogin()
				}
			},
			
			doLogin(){
				uni.request({
					url: reqAddress.DomainName + '/loginVerify/public_key',
					method:'POST',
					success: (res) => {
						this.public_key = "-----BEGIN PUBLIC KEY-----\n" + res.data.PublicKey + "\n-----END PUBLIC KEY-----";
						rsa.setPublicKey(this.public_key);
						let TimeStamp = res.data.TimeStamp;
						let user = this.loginForm.user;
						let pwd = rsa.encrypt(this.loginForm.pwd, 'base64');
						// console.log(pwd);
						uni.request({
							url: reqAddress.DomainName + '/login/pLogin',
							method: 'POST',
							data: {
								user : user,
								pwd: pwd,
								TimeStamp : TimeStamp
							},
							header: {
								'content-type': 'application/x-www-form-urlencoded'
							},
							success: (res) => {
								// console.log(res.header);
								if(res.data.errcode == 1){
									let sessionid = res.header['Set-Cookie'].substr(0,44);
									// console.log(sessionid);
									uni.setStorageSync('sessionid', sessionid);
									this.loading = false;
									uni.setStorageSync('LoginId', res.data.data.LoginId);
									uni.setStorageSync('Pname', res.data.data.Pname);
									uni.setStorageSync('RoleFlag', res.data.data.RoleFlag);
									
									this.afterlogin(0, res.data.data.LoginId);
									
									uni.showToast({
										title:'登录成功',
										duration: 2000
									});
									uni.reLaunch({
										url:'../../index/index'
									});
								}else if(res.data.errcode == 100){
									uni.showModal({
										title:'登录失败',
										content:'当前账号已在其他端登录，不能重复登录',
										showCancel:false,
										success: (res) => {
											if(res.confirm){
												uni.navigateBack({
													delta:2
												});
											}
										}
									})
								}else if(res.data.errcode == 105){
									uni.showModal({
										title:'登录失败',
										content:'用户名密码错误或用户不存在或已注销',
										showCancel:false,
										success: (res) => {
											if(res.confirm){
												uni.navigateBack({
													delta:1
												});
											}
										}
									})
								}
							},
							fail: () => {
								this.loading = false;
								uni.showToast({
									title:"请检查网络",
									icon:"none"
								})
							}
						})
					},
					fail: () => {
						this.loading = false;
						uni.showToast({
							title:"请检查网络",
							icon:"none"
						})
					}
				})
			},
			
			bindInput: function(e) {
				var dataval = e.currentTarget.dataset.val;
				this.loginForm[dataval] = e.detail.value;
			},
			
			passwordF_B() {
				this.hideEyes = !this.hideEyes;
			},
			formSubmit(e) {
				console.log(e.detail.value);
			},
			//检测手机号码格式
			checkMobile(mobile){
				return RegExp(/^\s{0}$|^1\d{10}$/).test(mobile);
			},
			
			afterlogin(type, loginId){
				//账号密码登录成功，type为0
				//账号密码退出成功，type为1
				uni.login({
					provider:'weixin',
					success: (res) => {
						const code = res.code;
						uni.request({
							url: reqAddress.DomainName + '/funcQuestion/WeChatLogIn/AfterLoginAndOut',
							method:'GET',
							data:{
								code, type, loginId
							},
							success: (res) => {
								console.log(res);	
							},
							fail: (err) => {
								console.log(err);
							}
						})
					}
				})
			}
		}
	}
</script>

<style>
	.login-form {
		margin: -9px 10px 0 10px;
		height: 200px;
		background: #007AFF;
		border-radius: 10px;
		background-color: #EEEEEE;
		box-shadow: 0 2px 10px #9B9B9B;
		padding: 20px;
	}
	
	.form-input input {
		background: #ffffff;
		border-radius: 5px;
		height: 60rpx;
		margin: 20px 0;
		padding: 0 10px;
		font-size: 40rpx;
	}
	
	.login-img {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 108px;
		margin-top: 100rpx;
	}
	
	.owl-login {
		width: 211px;
		height: 108px;
		background-image: url('data:image/png;base64,');
		background-repeat: no-repeat;
		background-size: 100%;
		position: absolute;
	}
	
	.owl-login .hand {
		width: 34px;
		height: 34px;
		-webkit-border-radius: 40px;
		border-radius: 40px;
		background-color: #472d20;
		transform: scaleY(0.6);
		transition: 0.3s ease-out;
		position: absolute;
		left: 14px;
		bottom: -8px;
	}
	
	.owl-login .hand.hand-r {
		left: 170px;
	}
	
	.owl-login .arms {
		top: 58px;
		position: absolute;
		width: 100%;
		height: 41px;
		overflow: hidden;
	}
	
	.owl-login .arms .arm {
		width: 40px;
		height: 65px;
		position: absolute;
		left: 20px;
		top: 40px;
		background-image: url("data:image/png;base64,");
		background-size: 100%;
		transition: 0.3s ease-out;
		transform: rotate(-20deg);
	}
	
	.owl-login .arms .arm.arm-r {
		transform: rotate(20deg) scaleX(-1);
		left: 158px;
	}
	
	.owl-login.password .hand {
		transform: translateX(42px) translateY(-15px) scale(0.7);
	}
	
	.owl-login.password .hand.hand-r {
		transform: translateX(-42px) translateY(-15px) scale(0.7);
	}
	
	.owl-login.password .arms .arm {
		transform: translateY(-40px) translateX(40px);
	}
	
	.owl-login.password .arms .arm.arm-r {
		transform: translateY(-40px) translateX(-40px) scaleX(-1);
	}
</style>
